import { MessageBox,Toast,Indicator } from 'mint-ui'
import qs from 'qs'
import axios from 'axios'


var Bucket = 'cos-1253286164';
var Region = 'ap-shanghai';
var cos;

export default {
  name: 'app',
  data () {
    return {
      // uid:12055999,
      // token:'5958625-ef7396d0709811e99d55525400b712ad',
      uid:sessionStorage.uid,
      token:sessionStorage.token,
      phone1:'',
      name:'',
      phone2:'',
      subFlag:false,
      popupVisible:false,
      pVisible:false,
      delFlag:false,
      values_index:0,
      values:{
        values0:"",
        values1:""
      },
      label:{
        label0:' Select',
        label1:' Select'
      },
      options: {
        tit:'',
        arr:[]
      },
      options0: {
        tit:'  Residence type',
        arr:[
          {
            label: ' Jobs',
            value: 1
          },
          {
            label: ' Learning',
            value: 2
          },
          {
            label: ' Reporter',
            value: 3
          },
          {
            label: ' Reunion',
            value: 4
          },
          {
            label: ' Personal issue',
            value: 5
          }
        ]
      },
      options1: {
        tit:'  Ralationship',
        arr:[
          {
            label: ' Parents',
            value: 1
          },
          {
            label: ' Couple',
            value: 2
          },
          {
            label: ' Friend',
            value: 3
          },
          {
            label: ' Teacher',
            value: 4
          },
          {
            label: ' Relative',
            value: 5
          },
          {
            label: ' Colleague',
            value: 6
          },
          {
            label: ' Other',
            value: 7
          }
        ]
      },
      photoTit:[
        ['','Passport homepage '],
        ['', 'Valid visa page'],
        ['','Immigration chapter page']
      ],
      picFunIndex:0,
      images:[],
      index:-1,
      uploadData:{
        images0:[],
        index0:-1,

        images1:[],
        index1:-1,

        images2:[],
        index2:-1,

        images3:[],
        index3:-1,

        images4:[],
        index4:-1,

        images5:[],
        index5:-1,

        images6:[],
        index6:-1,

        images7:[],
        index7:-1
      },
      imgSrc:{
        passport_img1:'',
        passport_img2:'',
        passport_img3:'',
        passport_img4:'',
      },
      selType:{
        selType1:'',
        selType2:''
      },
      house_code:'',
      lease:[]
    }
  },
  computed:{
    // 
    preImages(){
      return this.uploadData['images'+this.picFunIndex].map(v=>{return v.src});
    },
  },
  methods:{

    htmlScroll(){
      this.timer = setTimeout(() => {
        window.scrollTo(0,0)
        // 10
      },10)
    },
    handleVisibleChange (isVisible) {
        this.pVisible = isVisible;
    },
    getList(e){
      e==0?this.options = this.options0:this.options = this.options1;
       this.values_index=e;
       this.popupVisible=true;
    },
    radioFun(){
      for(var i=0;i<this.options.arr.length;i++){
        if(this.values['values'+this.values_index]==this.options.arr[i].value){
          this.label['label'+this.values_index]=this.options.arr[i].label
        }
      }
      this.selType['selType'+(this.values_index+1)]=this.values['values'+this.values_index];
      this.popupVisible=false;
      this.checkForm();
    },
    checkForm(){
      if(this.label1==' Select'||this.phone2==''||this.name==''||this.phone1==''||this.uploadData['images0'].length==0 || this.uploadData['images1'].length==0 || this.uploadData['images2'].length==0 ||this.label0==' Select' ||this.uploadData['images3'].length==0 || this.lease.length==0){
        this.subFlag = false;
      }else{
        this.subFlag = true;
      }
    },
    subIng(){
      var house_code =  window.location.hash.split('=')[window.location.hash.split('=').length-1];
      let that =this;
      Indicator.open('...');
      let data = {
        "passport_img1": that.imgSrc['passport_img1'],
        "passport_img2": that.imgSrc['passport_img2'],
        "passport_img3": that.imgSrc['passport_img3'],
        "reside_type": that.values['values0'],
        "reside_file": that.imgSrc['passport_img4'],
        "phone_number": this.phone1,
        "contact_xm": this.name,
        "contact_phone": this.phone2,
        "contact_relation": that.values['values1'],
        "house_code": this.house_code,
        "lease": this.lease.join(',')
      }
      console.log(data);
     this.axios({
        method: 'put',
        url: '/foreigner/reside/apply',
        headers:{
          'uid':that.uid,
          "token":that.token
        },
        data: qs.stringify(data)
      }).then(function (res) {
        Indicator.close();
        if(res.data.error==0){
          Toast({
            message: '',
            duration: 1500
          });
          setTimeout(function(){
            location.replace('#/login');
          },2000)
        }else{
          Toast({
            message: res.data.message,
            duration: 1500
          });
        }
      })
      .catch(function (error) {
        Toast({
          message: '',
          duration: 1500
        });
        Indicator.close();
        console.log(error.message);
      });
    },

    formFun(){
      let that =  this;
      MessageBox({
       title: '',
       message: 'Please confirm that your information is entered correctly',
       showCancelButton: true,
       cancelButtonText:' Cancel',
       confirmButtonText:' Confirm'
     }).then(action => {
      if (action == 'confirm') {   //
         that.subIng();
      }
    });
    },
    picFun(e){
      this.picFunIndex=e;
      this.images=this.uploadData['images'+this.picFunIndex];
    },
    popFun(){
      this.popupVisible=false;
    },
    /**
     *   -- 
     */
     bindtap_chooseImages(e){
       let that = this;
       this.checkForm();
       if (Array.isArray(e)) {
         let file = e[0].file
         console.log(file);
         Indicator.open('...');
         cos.sliceUploadFile({
              Bucket: Bucket,
              Region: Region,
              Key: 'alarm/foreigner/'+(new Date()).getTime()+'.jpg',
              Body: file,
              TaskReady: function(taskId) {                   /*  */
                  //console.log("11111"+taskId);
              },
              onHashProgress: function (progressData) {       /*  */
                  //console.log("11112"+JSON.stringify(progressData));
              },
              onProgress: function (progressData) {           /*  */
                  //console.log("11113"+JSON.stringify(progressData));
              }
          }, function (err, data) {

            if(data.statusCode==200){
              Indicator.close();
              that.uploadData['images'+that.picFunIndex] = that.uploadData['images'+that.picFunIndex].concat(e);
              console.log(that.uploadData['images0']);
              if(that.picFunIndex<4){
                that.imgSrc['passport_img'+(that.picFunIndex+1)]="https://"+data.Location;
                //that.uploadData['images'+that.picFunIndex].push("https://"+data.Location);
              //  console.log(that.uploadData['images'+that.picFunIndex]);
              }else{
                console.log(that.lease);
                that.lease.push("https://"+data.Location);
              }
              that.checkForm();
            }else {
              Toast({
                message: '',
                duration: 1500
              });
            }
          });

       }else {
         console.log(res);
       }
     },
    /**
     *   -- 
     */
    bindtap_delete(){
      this.images = this.images.filter((v,i) => {
        return this.index!=i;
      })
    },
    /**
     *   -- 
     */
    bingtap_preview(i,j){
      this.delFlag = true;
      this.picFunIndex=i;
       this.uploadData['index'+i] = j;
    },
    /**
     *   -- 
     */
    bingtap_hiddenImg() {
      this.delFlag = false;
      this.uploadData['index'+this.picFunIndex] = -1;
    },
    /**
     *   -- 
     */
     delFun(){
       if(this.picFunIndex<4){
         this.imgSrc['passport_img'+(this.picFunIndex+1)]='';
       }else{
         this.lease = this.lease.splice(1,1);
       }
       this.delFlag = false;
       this.uploadData['index'+this.picFunIndex] = -1;
       this.uploadData['images'+this.picFunIndex]=[];
       this.checkForm();
     }
  },

  created:function(){
    console.log(this.uid);
    console.log(this.token);
    let that = this;
    Indicator.open('');
    that.axios({
       method: 'get',
       url: '/foreigner/reside/status',
       headers:{
         'uid':that.uid,
         "token":that.token
       },
       //data: qs.stringify(data)
     }).then(function (res) {
       Indicator.close();

       if(res.data.error==0){
         let data = res.data;
         let status='';
         if(data.data.status==0){
           status=''
         }else if(data.data.status==1){
           status=''
         }
         else if(data.data.status==2){
           status=''
         }
         console.log(that.uploadData.images0);
        that.uploadData.images0.push({"src":data.data.apply_data.passport_img1});
        that.imgSrc['passport_img1']=data.data.apply_data.passport_img1;
         that.uploadData.images1.push({"src":data.data.apply_data.passport_img2});
         that.imgSrc['passport_img2']=data.data.apply_data.passport_img2;
         that.uploadData.images2.push({"src":data.data.apply_data.passport_img3});
         that.imgSrc['passport_img3']=data.data.apply_data.passport_img3;
         that.uploadData.images3.push({"src":data.data.apply_data.reside_file});
         that.imgSrc['passport_img4']=data.data.apply_data.reside_file;
         that.values['values0']=data.data.apply_data.reside_type+'';
         that.phone1=data.data.apply_data.phone_number;
         that.name=data.data.apply_data.contact_xm;
         that.phone2=data.data.apply_data.contact_phone;
         that.values['values1']=data.data.apply_data.contact_relation+'';
         that.lease = data.data.apply_data.lease.split(',');
         that.house_code = data.data.apply_data.house_code;
         for(var i=0;i<that.lease.length;i++){
           that.uploadData['images'+(i+4)].push({"src":that.lease[i]});
         };
         for(var i=0;i<that.options0.arr.length;i++){
           if(that.values['values0']==that.options0.arr[i].value){
             that.label['label0']=that.options0.arr[i].label
           }
         }
         for(var i=0;i<that.options1.arr.length;i++){
           if(that.values['values1']==that.options1.arr[i].value){
             that.label['label1']=that.options1.arr[i].label
           }
         }
         //console.log(that.uploadData);
         //"house_code": house_code,
         // Toast({
         //   message: status,
         //   duration: 1500
         // });

      }else {
        Toast({
          message: res.data.message,
          duration: 1500
        });
      }

     })
    cos = new COS({
        getAuthorization: function (options, callback) {
            // 
            that.axios({
               method: 'get',
               url: '/upload/cos/auth',
               headers:{
                 'uid':that.uid,
                 "token":that.token
               },
               //data: qs.stringify(data)
             }).then(function (res) {
               Indicator.close();
               let data = res.data;
               callback({
                    TmpSecretId: data.credentials.tmpSecretId,
                    TmpSecretKey: data.credentials.tmpSecretKey,
                    XCosSecurityToken: data.credentials.sessionToken,
                    ExpiredTime: data.expiredTime,
               });
             })
        }
    });

  }
}



