import store from '../store.js'
import { MessageBox,Toast,Indicator } from 'mint-ui'

export default {
  name: 'app',
  data () {
    return {
      uid:'',
      token:'',
      // msg: 'Welcome to Your Vue.js App'
      listJson:[
        {
          data:'1',
          //title:store,
          title:'',
          con:''
        },
        {
          data:'2',
          title:'',
          con:''
        },
        {
          data:'3',
          title:'',
          con:''
        }
      ]
    }
  },
  methods: {
    toInfo(e){

      // this.uid= 9;
      // this.token="75-6ebfb2265d9211e982465254001de7b2";
      let that = this;


      if(e==1){
        that.$router.push({
          name:'procedure'
        })
      }else if(e==2){
        that.$router.push({
          name:'update'
        })
      }else if(e==3){

      }else{

      }
    },
    getResult(){
      Indicator.open('');
      let that =this;
      that.axios({
         method: 'get',
         url: '/foreigner/reside/status',
         headers:{
           'uid':that.uid,
           "token":that.token
         },
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         console.log(res);
         if(res.data.error==0){
           let data = res.data;
           let status='';
           if(data.data.e_signature==''){
             Toast({
               message: ' No information available',
               duration: 1500
             });
           }else{
             sessionStorage.resultSrc = data.data.e_signature;
             that.$router.push({
               'name':'result'
             })
           }
        }else {
          Indicator.close();
          Toast({
            message: res.data.message,
            duration: 1500
          });
        }

       })
    },
    cancelFun(){
      Indicator.open('');
      this.axios({
         method: 'get',
         url: '/foreigner/reside/cancel',
         headers:{
           'uid':this.uid,
           'token':this.token
         },
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.error==0){
           Toast({
             message: ' Your information was successfully logged out',
             duration: 1500
           });
         }else {
           Toast({
             message: res.data.message,
             duration: 1500
           });
         }
       })
    }
  },
  created:function(){
    Indicator.close();
  }
}



